:root {
  --color-primary: #015dc7;
  --color-primary-lighter: #2d8ee9;
  --color-text: #2f2c2c;
  --color-text-2: #545454;
  --color-container-1: #f5f5f5;
  --color-white-1: #ffffff;
  --color-white-2: #e6e6e6;
  --color-white-hover: #cccccc;
  --color-black-1: #333232;
  --color-stroke: #b0b7ca;
  --gradient-sky: linear-gradient(0deg, #b7e4f9, #e1f3fd);
  --font-1: "Montserrat", sans-serif;
}

html {
  scroll-behavior: auto !important;
}
html body {
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
}
html body,
html body * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html body .txt-bold {
  font-weight: bold;
}

@media (min-width: 0px) {
  html body .header {
    background-color: var(--color-white-1);
    position: fixed;
    width: 100vw;
    z-index: 100;
  }
  html body .header .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px 0px;
    max-width: 970px;
  }
  html body .header .container .logo {
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
    text-decoration: none;
  }
  html body .header .container .logo img {
    display: block;
    width: 100%;
  }
  html body .header .container .logo p {
    font-family: var(--font-1);
    color: var(--color-text);
    font-weight: bold;
  }
  html body .header .container .menu {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
  html body .header .container .menu .list {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 20px;
    margin-right: 0px !important;
  }
  html body .header .container .menu .list li {
    list-style-type: none;
    padding: 5px 0px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  html body .header .container .menu .list li button {
    text-decoration: none;
    color: var(--color-text);
    font-weight: bold;
    transition: 0.2s all;
    padding: 0px;
    text-transform: uppercase;
    border: none;
    background-color: transparent;
    font-size: 11px;
  }
  html body .header .container .menu .list li button:hover {
    color: var(--color-white-hover);
  }
  html body .header .container .menu .list li button:before {
    content: "";
    background-color: var(--color-white-hover);
    display: block;
    width: 0;
    height: 3px;
    transition: width 0.3s;
    margin: 0;
    border-top: 0;
    border: none;
    border-radius: 5px;
    position: absolute;
    left: 0;
    margin: auto;
    bottom: 0;
  }
  html body .header .container .menu .list li button:hover:before {
    width: 100%;
  }
  html body .header .container .menu .list li.item-register .register-btn {
    border: solid 4px var(--color-primary);
    background-color: transparent;
    padding: 5px 20px;
    color: var(--color-primary);
    text-transform: uppercase;
    transition: 0.25s all;
  }
  html body .header .container .menu .list li.item-register .register-btn:before {
    content: "";
    background-color: transparent;
    display: block;
  }
  html body .header .container .menu .list li.item-register:hover .register-btn {
    border: solid 4px var(--color-primary-lighter);
    color: var(--color-primary-lighter);
  }
  html body .header .container .menu_toggle {
    display: none;
    background-color: var(--color-white-1);
    border: none;
  }
  html body .header .container .menu_toggle .hamburger {
    width: 30px;
    height: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 6px;
  }
  html body .header .container .menu_toggle .hamburger .line {
    width: 30px;
    height: 6px;
    background-color: var(--color-text);
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translate(0, 0) rotate(180deg);
    transition: 0.5s all;
  }
  html body .header .container .menu_toggle.is-active .line-1 {
    top: 50%;
    transform: translate(0, -50%) rotate(135deg);
  }
  html body .header .container .menu_toggle.is-active .line-2 {
    width: 0;
  }
  html body .header .container .menu_toggle.is-active .line-3 {
    top: -50%;
    transform: translate(0, 50%) rotate(225deg);
  }
}
@media (max-width: 768px) {
  html body .header .container {
    padding: 10px 20px !important;
  }
  html body .header .container .logo img {
    width: 100%;
  }
}
@media (max-width: 992px) {
  html body .header .container .menu {
    position: absolute;
    background-color: var(--color-white-1);
    z-index: 100;
    top: 100%;
    padding: 10px 120px 10px 20px;
    height: 100vh;
    flex-direction: column;
    justify-content: flex-start;
    transition: right 0.5s;
  }
  html body .header .container .menu.hidden {
    right: -500px;
  }
  html body .header .container .menu.show {
    right: 0;
  }
  html body .header .container .menu .list {
    width: 100%;
    flex-direction: column;
    align-items: baseline;
    gap: 10px;
  }
  html body .header .container .menu_toggle {
    display: block;
  }
}
@media (max-width: 1200px) {
  html body .header {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  html body .header .container {
    width: 1200px;
    margin: 0 auto;
    padding: 10px 30px;
  }
  html body .header .container .menu .list {
    gap: 20px;
  }
}
