  html body .popup-register .form form .btn-continue {
    background-color: var(--color-primary);
    text-transform: uppercase;
    font-size: 20px;
    font-family: var(--font-1);
    font-weight: bold;
    color: var(--color-white-1);
    border: none;
    padding: 15px 15px;
