    text-align: center;
  }
  html body .popup-register .form form .btn-continue:hover {
    background-color: var(--color-primary-lighter);
  }
  html body .popup-register .form .disclaimer {
    text-align: center;
  }
  html body .popup-register .form .disclaimer p {
