    color: var(--color-white-hover);
  }
}
@media (min-width: 0px) and (max-width: 640px) {
  html body .popup-register {
    width: calc(100% - 40px);
    margin: 20px;
    left: 0;
    transform: translate(0%, -50%);
    flex-direction: column;
  }
  html body .popup-register .instructions {
    padding: 20px 20px;
  }
  html body .popup-register .form {
    width: 100%;
    padding: 20px 20px;
  }
  html body .popup-register .form .label {
    display: none;
  }
}
