    height: 100vh;
  }
  html body .blur_filter.blur_filter-hidden {
    z-index: -1;
    opacity: 0;
  }
  html body .blur_filter.blur_filter-showing {
    z-index: 150;
    opacity: 0.7;
  }
}
html body {
  position: relative;
}
html body.disable-scroll {
  overflow-y: hidden;
}
html body .filter {
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: black;
  transition: 0.35s opacity;
}
html body .filter-hidden {
  z-index: -1;
  opacity: 0;
}
html body .filter-fade_in {
  z-index: 50;
  opacity: 0;
}
html body .filter-fade_out {
  z-index: 50;
  opacity: 0;
}
html body .filter-show {
  z-index: 50;
  opacity: 0.7;
}

